

.home{
    display: grid;
    grid-template-rows: 30px 1fr 110px;
    width: 100%;
    height: calc(100vh - 50px);
    gap: 10px;
    padding: 10px;
}
.top{
    width: 100%;
    height: 40px;
    display: flex;
    grid-row: 1 / 2;
    padding: 0 15px;
}
.top .current-time{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.top .date{
    display: flex;
    gap: 10px;
    align-items: center;
}

/* pinned tasks */
.pinned{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    gap: 10px;
    padding: 10px 5px;
    border-radius: 5px;
    overflow: hidden;
    grid-row: 2 / 3;
}
.pinned-tasks-container{
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 5px;
    overflow-y: auto;
    overflow-x: hidden;
}
.home-task{
    width: 100%;
    height: 40px;
    min-height: 40px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: #0F0F0F;
    border-radius: 5px;
    padding: 0 15px;
}
.home-task .task-title{
    width: auto;
    max-width: calc(100% - 100px);
    justify-content: start;
    height: fit-content;
}
.home-task .task-dueDate{
    width: 100px;
    opacity: 0.5;
    height: fit-content;
    display: flex;
    align-items: center;
    justify-content: end;
}
.home .no-tasks-message{
    margin-left: 15px;
}
/* habbit */
.habbit{
    width: 100%;
    height: 110px;
    margin-top: auto;
    margin-bottom: 15px;
    padding: 10px 0;
    border-radius: 5px;
    grid-row: 3 / 4;
    gap: 15px;
    display: flex;
    flex-direction: column;
}
.habbit h3{
    padding-left: 10px;
}
.habbit-container{
    width: 100%;
    height: 50px;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    background-color: #0F0F0F;
    border-radius: 5px;
    align-self: center;
}
.week-day{
    height: 50px;
    width: 50px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border-radius: 5px;
}
.week-day-name{
    color: white;
    opacity: 0.7;
}
.selected-day p{
    color: #ff4400;
    opacity: 1;
}


